@use "base/variables" as v;
@use "base/mixins" as m;

.fa-solid {
  text-align: center;
  font-size: 2rem;
  @include m.tablet {
    font-size: 3rem;
  }
  @include m.desktopXL {
    font-size: 4rem;
  }
}

.links-news {
  text-align: center;
  .link {
    padding-bottom: 2vh;
  }
}

.main-links {
  margin-top: 5vh;
}

.links-research {
  text-align: center;
  @include m.telefono {
      display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  }
  .imagen {
      padding-top: 2rem;
      padding-bottom: 2rem;
  }
}

.links {
  address {
    margin: 0;
  }
  h3 {
    text-align: center;
    padding-bottom: 2rem;
  }
  a {
    font-size: 1.3rem;
    @include m.telefono {
      font-size: 1.4rem;
    }
    @include m.tablet {
      font-size: 1.6rem;
    }
    @include m.desktop {
      font-size: 1.7rem;
    }
  }
}

@include m.telefono {
  .links-news {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    .link {
      display: grid;
      grid-template-rows: 2fr 1fr;
    }
  }
}

.link {
  padding-bottom: 2.5rem;
}
