@use 'base/variables' as v;
@use 'base/mixins' as m;

.videos {
    display: grid;
    margin-bottom: 0;
    iframe {
        width: 80%;
        height: auto;
        margin: 0 auto 2rem auto;
    }
    @include m.telefono {
        grid-template-columns: 1fr 1fr;
        iframe {
            width: 90%;
            height: 12rem;
        }
    }
    @include m.tablet {
        iframe {
            height: 17rem;;
        }
    }
    @include m.desktop {
        grid-template-columns: 1fr 1fr 1fr;
        iframe {
            height: 20rem;
        }
    }
    @include m.desktopXL {
        iframe {
            height: 25rem;
        }
    }
}