@use 'base/variables' as v;
@use 'base/mixins' as m;

.collaboration-title {
    margin-bottom: 3rem;
}

.collaboration-text {
    p{
        text-align: center;
        margin: 1.5rem auto;
    }
    .researcher-name {
        color: v.$secundario;
        display: block;
        @include m.tablet {
            display: unset;
        }
    }
    .hyphen {
        display: none;
        @include m.tablet {
            display: inline;
        }
    }
}