@use "base/variables" as v;
@use "base/mixins" as m;

.about {
  .imagen {
    width: 15rem;
    padding-bottom: 4rem;
  }
  padding: 2rem;
  @include m.telefono {
    display: flex;
    .imagen {
        flex: 0 0 30%;
        margin-left: 0;
    }
    .text {
        flex: 0 0 65%;
    }
  }
  @include m.desktopXL {
    .imagen {
        flex: 0 0 20%;
        margin-left: 0;
    }
    .text {
        flex: 0 0 77%;
    }
  }
  @include m.desktopXL {
    width: 95%;
    margin-left: auto;
    margin-right: auto;
  }
}

.about-extra {
  padding: 0 2rem 2rem 2rem;
  p {
      @include m.desktopXL {
          text-align: center;
      }
  }
}
.image-mail {
  padding-bottom: 2rem;
  width: 8rem;
}

.image-biomechatronics {
  width: 16rem;
}

.image-researchgate {
  width: 12rem;
}

.image-mail,
.image-biomechatronics,
.image-researchgate {
  margin-top: 2rem;
  padding-bottom: 2rem;
}
